.downloads {

  &__lead {
    padding: 100px 0;

    &-container {
      display: flex;
      justify-content: space-between;
    }

    &-intro-container {
      width: 50%;
    }

    &-title {
      margin: 10px 0 40px;
      padding: 0;
    }

    &-text {
      max-width: 400px;
    }

    &-buttons-list {
      list-style: none;
      margin: 40px 0 10px 0;
      padding: 0;
      display: flex;
      flex-direction: row;

      &-item {
        margin: 0 30px 0 0;
        padding: 0;

        &:last-of-type {
          margin-right: 0;
        }
      }
    }

    &-button {
      display: inline-block;
      text-indent: -999999px;
      text-decoration: none;
      width: 180px;
      height: 52px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;

      &_google {
        background: url("../../../assets/images/googleplay.svg");
      }

      &_appstore {
        background: url("../../../assets/images/appstore.svg");
      }
    }

    &-image-container {
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &-image-wrapper {
      max-width: 508px;
    }

    &-image {
      width: 100%;
      object-fit: contain;
    }
  }

  &__content {
    padding: 100px 0 200px 0;

    &-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    &-flexbox {
      width: 50%;
      height: auto;

      &:last-of-type {
        display: flex;
        justify-content: center;
      }
    }

    &-title-wrapper {
      max-width: 593px;
    }

    &-title {
      font-size: 32px;
      line-height: 39px;
      font-weight: 700;
      letter-spacing: -0.02em;
      margin: 0 0 60px;
      padding: 0;
      text-transform: lowercase;
    }

    &-text-wrapper {
      font-size: 18px;
      line-height: 22px;
      color: #222222;
      max-width: 400px;
    }

    &-text {
      margin: 0 0 20px;
      padding: 0;
      &:last-of-type {
        margin-bottom: 0;
      }
    }

    &-list {
      list-style: none;
      margin: 0 0 20px;
      padding: 0;

      &-item {
        margin: 0 0 20px;
        padding: 0 0 0 36px;
        display: block;
        background-image: url("../../../assets/images/icon-check-blue.svg");
        background-repeat: no-repeat;
        background-size: 24px 24px;
        background-position: center left;
      }
    }
  }
}